import React, { Component } from 'react';
import './character.scss';
import { NavLink } from 'react-router-dom';
import charactersList from './../CharactersList';

const CharacterItem = (props) => (
  <li>
    <div className="character__item">
      <NavLink to={props.link} className={props.characterclass}>
        <span className="character__name">
          {props.name}
        </span>
      </NavLink>
    </div>
  </li >
)

class Character extends Component {


  constructor(props) {
    super(props);
    this.state = {

    }
  }

  getCharacters = () => {
    const { element, weapon, title } = this.props
    let characters = charactersList

    if (element) characters = characters.filter((el) =>
      el.element === element
    );
    if (weapon) characters = characters.filter((el) =>
      el.weapon === weapon
    );
    if (title) characters = characters.filter((el) =>
    el.charactername.toLowerCase().includes(title.toLowerCase())
  );
    return characters
  }

  render() {

    return (
      <ul className="character__list">
        {this.getCharacters().map((ch) => {
          return (
            <CharacterItem
              key={ch.charactername}
              characterclass={ch.characterclass}
              name={ch.charactername}
              link={ch.link}
            />
          )
        })}
      </ul>
    )
  }
}

export default Character;