import React, { Component } from 'react';
import { render } from '@testing-library/react';
import './Map.scss';
import mapEn from './map-enemies.png';
import mapOr from './map-ore.png';
import mapPlants from './map-plants.png';
import mapShrines from './map-shrines.png';
import MapListBtn from './MapListBtn';







class Map extends Component {

  constructor(props) {
    super(props);
    this.state = {
      mapId: mapEn,
    }
  }


  changeIdMapHanler = (mapId) => () => {
    this.setState({
      mapId,
    })
  }

  render() {

    return (
      <div className="map main">
        <ul className="map__list">
          <MapListBtn
            buttonName="Противники"
            isActive={this.state.mapId === mapEn }
            onClick={this.changeIdMapHanler(mapEn)}
          />
          <MapListBtn
            buttonName="Руда"
            isActive={this.state.mapId === mapOr }
            onClick={this.changeIdMapHanler(mapOr)}
          />
          <MapListBtn
            buttonName="Растения"
            isActive={this.state.mapId === mapPlants }
            onClick={this.changeIdMapHanler(mapPlants)}
          />
          <MapListBtn
            buttonName="Усыпальницы"
            isActive={this.state.mapId === mapShrines }
            onClick={this.changeIdMapHanler(mapShrines)}
          />
        </ul>
        <div className="map__img-wrapper">
          <img src={this.state.mapId} className="Map__img" />
        </div>
      </div >
    )
  }
}








export default Map;