import React, { Component } from 'react';
import './footer.scss';
import { render } from '@testing-library/react';



const List = (props) => (
  <li className="footer__item">
    <a href="#" className="footer__link">
      {props.name}
    </a>
  </li>
)



class Flist extends Component {


  constructor(props) {
    super(props);
    this.state = {
      Listname1: [
        {
          name: 'Contacts'
        },
        {
          name: 'Privat Policy'
        },
        {
          name: 'Manage Setting'
        }
      ],
      Listname2: [
        {
          name: 'Discord'
        },
        {
          name: 'Reddit'
        },
        {
          name: 'Faceook'
        }
      ]
    }
  }

  render() {

    return (
      <div className="footer__r">
        <div className="footer__listing">
          <span className="footer__listing-name">
            More Links
        </span>
          <ul className="footer__list">
            {
              this.state.Listname1.map((l1, index) => {
                return (
                  <List
                    key={index}
                    name={l1.name} />
                )
              })
            }
          </ul>
        </div>
        <div className="footer__listing">
          <span className="footer__listing-name">
            Community Links
        </span>
          <ul className="footer__list">
            {
              this.state.Listname2.map((l2, index) => {
                return (
                  <List
                    key={index}
                    name={l2.name} />
                )
              })
            }
          </ul>
        </div>
      </div>
    )
  }
}










export default Flist;
