import React, { Component } from 'react';
import { render } from '@testing-library/react';






const MapListBtn = ({ buttonName, onClick, isActive }) => {


  return (
    <li className={`map__list-item ${isActive ? 'active' : ''}`}>
      <button onClick={onClick} className="map__list-btn">
        {buttonName}
      </button>
    </li>
  )
}















export default MapListBtn;