import React, { Component } from 'react';
import './tools.scss';


const ToolsItems = (props) => (
  <li className={`tools__list-item ${props.isActive ? 'active' : ''}`} >
    <button aria-label="tools button" onClick={props.onClick} type="button" className={props.btnclass} />
  </li>
)




class Tools extends Component {


  constructor(props) {
    super(props);
    this.state = {
      ToolsItemLeft: [
        {
          btnclass: 'tools__list-btn anemo',
          element: 'anemo'
        },
        {
          btnclass: 'tools__list-btn cryo',
          element: 'cryo'
        },
        {
          btnclass: 'tools__list-btn electro',
          element: 'electro'
        },
        {
          btnclass: 'tools__list-btn dendro',
          element: 'dendro'
        },
        {
          btnclass: 'tools__list-btn geo',
          element: 'geo'
        },
        {
          btnclass: 'tools__list-btn hydro',
          element: 'hydro'
        },
        {
          btnclass: 'tools__list-btn pyro',
          element: 'pyro'
        }
      ],
      ToolsItemRight: [
        {
          btnclass: 'tools__list-btn bow',
          weapon: 'bow',
        },
        {
          btnclass: 'tools__list-btn boock',
          weapon: 'boock',
        },
        {
          btnclass: 'tools__list-btn bigsword',
          weapon: 'bigsword',
        },
        {
          btnclass: 'tools__list-btn spear',
          weapon: 'spear',
        },
        {
          btnclass: 'tools__list-btn sword',
          weapon: 'sword',
        },
      ]
    }
  }


  render() {

    const {
      element, weapon 
    } = this.props

    return (
      <div className="tools">
        <ul className="tools__list tools__list-left">
          {this.state.ToolsItemLeft.map((lItem) => {
            return (
              <ToolsItems
                key={lItem.element}
                isActive={lItem.element === element}
                btnclass={lItem.btnclass}
                onClick={() => this.props.setElement(lItem.element === this.props.element ? null : lItem.element)}
              />
            )
          })}
        </ul>
        <ul className="tools__list tools__list-right">
          {this.state.ToolsItemRight.map((rItem) => {
            return (
              <ToolsItems
                key={rItem.weapon}
                isActive={rItem.weapon === weapon}
                btnclass={rItem.btnclass}
                onClick={() => this.props.setWeapon(rItem.weapon === this.props.weapon ? null : rItem.weapon)}
              />
            )
          })}
        </ul>
      </div>
    )
  }
}

















export default Tools;