import React, { Component } from 'react';
import './header.scss';
import { render } from '@testing-library/react';
import logo from './genshin-logo.svg';
import { NavLink, Link } from 'react-router-dom';



const HeaderListItem = (props) => (
  <li className="header__list-item">
    <NavLink to={props.link} className="header__list-link">
      {props.linkname}
    </NavLink>
  </li>
)

const HeaderLogotype = () => (
  <div className="header__logotype">
    <Link to="/main" className="header__logotype-link">
      <img alt="Genshin Impact Informations site" src={logo} className="header__logotype-img" />
    </Link>
    <NavLink to="/main" className="header__logotype-link">
      GENSHIN
    </NavLink>
  </div>
)


class Header extends Component {


  constructor(props) {
    super(props);
    this.state = {
      isToggleOn: true,
      HeaderList: [
        {
          name: 'Персонажи',
          link: '/main'
        },
        {
          name: 'Команды',
          link: '/teams'
        },
        {
          name: 'Тир Лист',
          link: '/tearlist'
        },
        {
          name: 'Карта',
          link: '/map'
        },
        {
          name: 'База данных',
          link: '/database'
        }
      ]
    };
    this.handleClick = this.handleClick.bind(this);
    // this.handleClick2 = this.handleClick2.bind(this);
  }

  handleClick() {
    this.setState(state => ({
      isToggleOn: !state.isToggleOn
    }));

  }
  // handleClick2() {
  //   this.setState(state => ({
  //     isToggle2On: !state.isToggle2On
  //   }));
  // }
  render() {

    return (
      <div className="header">
        <HeaderLogotype />
        <div className="heade__aside">
          <ul className={this.state.isToggleOn ? 'header__list' : 'header__list active'}>
            {
              this.state.HeaderList.map((hItem, index) => {
                return (
                  <HeaderListItem
                    key={index}
                    linkname={hItem.name} 
                    link={hItem.link}
                    />
                )
              })
            }
          </ul>
          <div className="header__burger">
            <button aria-label="burger-menu" className={this.state.isToggleOn ? 'burger' : 'burger active'} onClick={this.handleClick}>
              <span className={this.state.isToggleOn ? 'burger-span1' : 'burger-span1 active'}></span>
              <span className={this.state.isToggleOn ? 'burger-span2' : 'burger-span2 active'}></span>
              <span className={this.state.isToggleOn ? 'burger-span3' : 'burger-span3 active'}></span>
            </button>
          </div>
        </div>
      </div>
    )
  }
}


























export default Header;
