import React from 'react';
import './title.scss';






const Title = ({title, setTitle}) => (
  <div className="main__title">
    <h1 className="main__title-text">Genshin Impact - Список персонажей</h1>
    <div className="search-box">
    <button aria-label="search-submit" className="main__title-btn" type="submit" />
      <div className="main__title-form">
        <input aria-label="Search through site content" id="search" value={title} onChange={(e) => setTitle(e.target.value)} autoComplete="off" className="main__title-input" type="text" placeholder="Введите имя" name="search" />
        <label htmlFor="saecrh" className="nope">Введите имя</label>
      </div>
      <button aria-label="search-close" onClick={() => setTitle('')} className="main__title-close" type="button" />
    </div>
  </div>
)










export default Title;