import React, { Component } from 'react';
import CharacterItem from './TearListItem';
import characterList from './../CharactersList';
import TearListWrapperItem from './TearListWrapperItem';








class TearListWrapper extends Component {


  constructor(props) {
    super(props);
    this.state = {
      tearWrapper: [
        {
          rankName: `S`,
          className: 'tearlist__rank tearlist__rank-s'
        },
        {
          rankName: `A`,
          className: 'tearlist__rank tearlist__rank-a'
        },
        {
          rankName: `B`,
          className: 'tearlist__rank tearlist__rank-b'
        },
        {
          rankName: `C`,
          className: 'tearlist__rank tearlist__rank-c'
        },
        {
          rankName: `D`,
          className: 'tearlist__rank tearlist__rank-d'
        }
      ]
    }
  }



  getCharacters = (rank) => {
    const { element, weapon, title } = this.props
    let characters = characterList

    if (element) characters = characters.filter((el) =>
      el.element === element
    );
    if (weapon) characters = characters.filter((el) =>
      el.weapon === weapon
    );
    if (rank) characters = characters.filter((el) =>
      el.rank === rank
    );
    if (title) characters = characters.filter((el) =>
      el.charactername.toLowerCase().includes(title.toLowerCase())
    );
    return characters
  }



  render() {

    return (
      <div className="tearlist__listing">
        {
          this.state.tearWrapper.map((rankwrap) => {
            return (
              <TearListWrapperItem
                rankName={rankwrap.rankName}
                className={rankwrap.className}
                key={rankwrap.rankName}
                >
                <div className="tearlist__list">
                  <ul className="tearlist-character">
                    {this.getCharacters(rankwrap.rankName).map((ch) => {
                      return (
                        <CharacterItem
                          key={ch.charactername}
                          characterclass={ch.characterclass}
                          name={ch.charactername}
                          link={ch.link}
                        />
                      )
                    })}
                  </ul>
                </div>
              </TearListWrapperItem>
            )
          })
        }
      </div>
    )
  }
}








export default TearListWrapper;