import React, { Component } from 'react';
import Tools from './../ToolFilter/Tools';
import TearListWrapper from './TearListWrapper';
import './TearList.scss'

const Title = ({ title, setTitle }) => (
  <div className="main__title">
    <h1 className="main__title-text">Genshin Impact - Тир Лист</h1>
    <div className="search-box">
      <button className="main__title-btn" type="submit" />
      <div className="main__title-form">
        <input value={title} onChange={(e) => setTitle(e.target.value)} autoComplete="off" className="main__title-input" type="text" placeholder="Search for a character" name="search" />
      </div>
      <button onClick={() => setTitle('')} className="main__title-close" type="button" />
    </div>
  </div>
)



class TearList extends Component {


  constructor(props) {
    super(props);
    this.state = {
      element: null,
      weapon: null,
      title: '',
    }
  }

  render() {

    return (
      <div className="tearlist main">
        <Title
          title={this.state.title}
          setTitle={(title) => this.setState({ title })}
        />
        <Tools
          setElement={(element) => this.setState({ element })}
          element={this.state.element}
          setWeapon={(weapon) => this.setState({ weapon })}
          weapon={this.state.weapon}
        />
        <TearListWrapper
          element={this.state.element}
          weapon={this.state.weapon}
          title={this.state.title}
        />
      </div>
    )
  }
}















export default TearList;